body {
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding-bottom: 1em;
}

.nasa--box {
    position: relative;
    top: 30px;
    background-image: linear-gradient(to right bottom, rgba(111, 172, 213, 0.8), rgba(83, 134, 211, 0.8));
    color: #fff;
    margin: 20px auto;
    width: 60%;
    text-align: center;
    box-shadow: 0 2px 3px #ccc;
    padding: 10px;
    box-sizing: border-box;
    box-shadow: 1px 4px 6px 1px rgba(0,0,0,0.2);
    transition: 0.3s;
    border-radius: 23px;
    line-height: 1;
}

h1 {
    -webkit-text-stroke: 1px black;
    color: white;
    text-shadow:
        3px 3px 0 #000,
        -1px -1px 0 #000,
        1px -1px 0 #000,
        -1px 1px 0 #000,
        1px 1px 0 #000;
    text-transform: uppercase;
    font-weight: 900;
    letter-spacing: 6px;
}

.items {
    position: relative;
    top: 30px;
    color: #fff;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    width: 80%;
    margin: auto;
}

.input--mod {
    text-align: center;
    margin-top: 1em;
    width: 35%;
    padding-top: 6px;
    padding-left: 2px;
    font-size: 24px;
    font-family: "Lato", sans-serif;
    font-weight: 700;
    letter-spacing: 1px; /* Firefox */ /* Safari, Chrome */ /* KHTML */
    border-radius: 50px; /* CSS3 */
}

.checkbox {
    display: inline-block;
}

.no--item {
    letter-spacing: 1px;
    font-weight: 400;
}

.space--right {
    margin-right: 2em;
}

input[type='checkbox'] {
    position: relative;
    -webkit-appearance:none;
    width:25px;
    height:25px;
    top: 7px;
    background:white;
    border-radius:5px;
    border:2px solid #555;
}
input[type='checkbox']:checked {
    background-image: linear-gradient(to right bottom, rgba(0, 255, 0, 0.8), rgba(255, 255, 255, 0.523));

}
.User {
    background-image: linear-gradient(to right bottom, rgba(111, 172, 213, 0.8), rgba(83, 134, 211, 0.8));
    width: 250px;
    padding: 16px;
    text-align: center;
    border: 1px solid #eee;
    box-shadow: 0 2px 3px #ccc;
    margin: 10px;
    box-sizing: border-box;
    cursor: pointer;
    box-shadow: 1px 4px 6px 1px rgba(0,0,0,0.2);
    transition: 0.3s;
    border-radius: 23px;
    font-size: 16px;
}
  
  .User:hover,
  .User:active {
    background-color: #C0DDF5;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  }
  
  .Details {
    margin: 16px 0;
    color: #fff;
  }
.Button {
    position: relative;
    background-color: transparent;
    border: none;
    color: white;
    outline: none;
    cursor: pointer;
    border-radius: 100px;
    font: inherit;
    padding: 10px !important;
    margin: 10px !important;
    font-weight: bold;
    text-align: center;
}
  
.Btn > * {
transition: all 0.2s;
}

.Button:first-of-type {
margin-left: 0;
padding-left: 0;
}

.Button:disabled {
color: #ccc;
cursor: not-allowed;
}

.Success {
background-image: linear-gradient(
    to right,
    #00cc00,
    #009900
);
box-shadow: 0 1rem 4rem rgba(0,0,0,.30);
}

    .Success:focus,
    .Success:hover {
    outline: none;
    -webkit-transform: translateY(-3px);
            transform: translateY(-3px);
    -webkit-animation: pulsate 1.5s infinite;
            animation: pulsate 1.5s infinite;
    }

        .Success::after {
            -webkit-transform: scaleX(1.4) scaleY(1.6);
                    transform: scaleX(1.4) scaleY(1.6);
            opacity: 0;
        }

            .Success:active,
            .Success:focus {
                outline: none;
                -webkit-transform: translateY(-1px);
                        transform: translateY(-1px);
                box-shadow: 0 1rem 4rem rgba(0,0,0,.30);
            }

.Danger {
    background-image: linear-gradient(
        to right,
        #fad84e,
        #ba9608
    );
    box-shadow: 0 1rem 4rem rgba(0,0,0,.30);
}
    
    .Danger:focus,
    .Danger:hover {
    outline: none;
    -webkit-transform: translateY(-3px);
            transform: translateY(-3px);
    -webkit-animation: pulsate 1.5s infinite;
            animation: pulsate 1.5s infinite;
    }

@-webkit-keyframes pulsate {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
        box-shadow: none;
    }

    50% {
        -webkit-transform: scale(1.10);
                transform: scale(1.10);
        box-shadow: 0 1rem 4rem rgba(0, 0, 0, 0.25);
    }

    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
        box-shadow: none;
    }
}

@keyframes pulsate {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
        box-shadow: none;
    }

    50% {
        -webkit-transform: scale(1.10);
                transform: scale(1.10);
        box-shadow: 0 1rem 4rem rgba(0, 0, 0, 0.25);
    }

    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
        box-shadow: none;
    }
}

