.item {
    background-image: linear-gradient(to right bottom, rgba(111, 172, 213, 0.8), rgba(83, 134, 211, 0.8));
    width: 650px;
    padding: 16px;
    text-align: center;
    border: 1px solid #eee;
    box-shadow: 0 2px 3px #ccc;
    margin: 10px;
    box-sizing: border-box;
    cursor: pointer;
    box-shadow: 1px 4px 6px 1px rgba(0,0,0,0.2);
    transition: 0.3s;
    border-radius: 23px;
    font-size: 16px;
}
  
  .item:hover,
  .item:active {
    background-color: #C0DDF5;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  }
  
.center--text {
  font-size: 20px;
  font-weight: 700;
  color: black;
}

.desc--text {
  font-size: 20px;
  letter-spacing: 0.7px;
}